  background-color: #20c997;
  border-color: rgba(0, 0, 0, 0.1);
}

.bottom-link {
  text-align: center;
  padding-top: 10px;
}

.home {
  text-align: center;
}

.home .img {
  font-size: 100px;
}

.home h1 span {
  font-weight: normal;
  font-size: 16px;
}


.logoApp {
  display: flex;
  justify-content: center;
}

.full-width-input {
  display: block;
  text-align: right;
}

.full-width-input input {
  width: 12em;
  padding: 4px 8px;
  margin: 4px 8px;
  border: 1px solid #aaaaaa;
  border-radius: 0;
}

.flex-parent {
  display: flex;
}

.jc-center {
  justify-content: center;
}

.center-block {
  display: block;
  margin-right: auto;
  margin-left: auto;
}

.errorMessg {
  display: block;
  margin-left: 70px;
}
